.modal {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.543);
}

.log-in {
  position: relative;
  align-self: flex-end;
  width: 100px;
  border: none;
  font-family: "Muli", sans-serif;
  font-weight: bolder;
  font-size: 12px;
  height: 32px;
  border-radius: 4px;
  color: white;

  outline: none;
  background-color: #ed3ad0;
  z-index: 500;
  margin-left: 80vw;
  -webkit-transition: 0.8s cubic-bezier(0.2, 0.82, 0.2, 1);
  transition: 0.8s cubic-bezier(0.2, 0.82, 0.2, 1);
  margin-top: 28px;
}

.login-txt {
  font-family: "Muli", sans-serif;
}

.log-in:focus {
  outline: none;
}

.log-in:hover {
  box-shadow: 0px 0px 50px rgba(237, 58, 208, 0.5);
  -webkit-transform: scale(1.1, 1.1);
          transform: scale(1.1, 1.1);
}

.modal-main {
  position: absolute;
  font-family: "Muli", sans-serif;
  background: white;
  width: 40%;
  text-align: center;
  height: 100%;
  z-index: 0;
  padding: 28px 0px 48px 0px;
  border-radius: 12px;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

@media (max-width: 800px) {
  .modal-main {
    width: 60%;
  }
}

@media (max-width: 500px) {
  .modal-main {
    width: 70%;
    padding: 38px 0px 38px 0px;
  }
}

.modal-main button {
  margin-top: 12px;
  width: 180px;
  color: white;
  border-radius: 4px;
  font-family: "Muli" sans-serif;
  font-weight: bolder;
  font-size: 12px;
  border: none;
  background: #ed3ad0;
  height: 42px;
  -webkit-transition: 0.8s cubic-bezier(0.2, 0.82, 0.2, 1);
  transition: 0.8s cubic-bezier(0.2, 0.82, 0.2, 1);
}

.modal-main button:focus {
  outline: none;
}

.modal-main button:hover {
  -webkit-transform: scale(1.1, 1.1);
          transform: scale(1.1, 1.1);
}

.close-bttn {
  background: rgb(230, 230, 230) !important;
  color: rgb(159, 159, 159) !important;
}

.modal-main input {
  margin-top: 12px;
  width: 180px;
  height: 42px;
  padding: 14px;
  font-size: 12px;
  color: #333333;
  font-weight: bolder;
  border-color: rgb(233, 233, 233);
  border-width: 1.3px;
  border-style: solid;
  border-radius: 30px;
}

.modal-main input:focus {
  outline: none;
}

.modal-main input::-webkit-input-placeholder {
  color: rgb(196, 195, 195);
}

.modal-main input::-moz-placeholder {
  color: rgb(196, 195, 195);
}

.modal-main input::-ms-input-placeholder {
  color: rgb(196, 195, 195);
}

.modal-main input::placeholder {
  color: rgb(196, 195, 195);
}

